@import url(https://fonts.googleapis.com/css2?family=Poiret+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nav{padding:1vmin 10vmin;display:flex;font-family:'Poiret One', cursive;justify-content:space-between;align-items:center;background:rgba(0,0,0,0.5)}.nav__logo{height:26px}.nav__btn{font-family:'Poiret One', cursive;font-weight:bold;color:white;width:30px;height:30px;border-radius:50%;border:none;margin:0 1vmin;padding:0;position:relative}.nav__btn_wrapper{display:flex;align-items:center;transition:-webkit-filter 1s;transition:filter 1s;transition:filter 1s, -webkit-filter 1s}.nav__btn:focus{outline:none}.nav__btn:hover{cursor:pointer}.nav__btn_img{width:30px;height:30px;z-index:0;box-sizing:border-box;border-radius:50%}.nav__btn_img-active{border:2px solid white}

.NavBar{width:80vw;display:flex;justify-content:flex-end;align-items:center;text-align:center;z-index:2;padding:0 2vmin}.NavBar__link{padding:5px 10px;margin:0 2vmin;font-weight:bolder;letter-spacing:1px;text-decoration:none;position:relative;z-index:0;display:inline-block;overflow:hidden;color:#333}.NavBar__link_active{border-radius:3px;color:white;transition:left 1s}.NavBar__link_active::before{content:"";position:absolute;z-index:-1;top:0;left:0;transform:translateY(calc(100% - 2px));width:100%;height:100%;background-image:linear-gradient(60deg, black 0%, grey 100%);-webkit-animation:anim 0.4s;animation:anim 0.4s}.NavBar__link:hover{color:#fff}@-webkit-keyframes anim{from{left:-100%}to{left:0}}@keyframes anim{from{left:-100%}to{left:0}}

.MainPage__bg{position:absolute;left:0;bottom:0;max-height:calc(98vh - 30px)}

.About__bg{position:absolute;left:0;bottom:0;max-height:calc(98vh - 30px)}

.Appointment__bg{position:absolute;left:0;bottom:0;max-height:calc(98vh - 30px)}

.Preloader{height:100vh;width:100vw;display:flex;justify-content:center;align-items:center;background-color:black;cursor:none}.Preloader__header{margin:0;font-size:10vw;color:rgba(225,225,225,0.01);background-image:url("https://raw.githubusercontent.com/vitaliikorol/med/master/public/images/preloader.jpg");background-repeat:repeat;background-size:cover;-webkit-background-clip:text;-webkit-animation:text_bg 10s ease-in-out infinite;animation:text_bg 10s ease-in-out infinite;text-align:center;text-transform:uppercase;font-weight:900}.Preloader__description{color:white;position:absolute;bottom:0;-webkit-animation:description 2s infinite;animation:description 2s infinite}@-webkit-keyframes description{0%{color:white}50%{color:grey}100%{color:white}}@keyframes description{0%{color:white}50%{color:grey}100%{color:white}}@-webkit-keyframes text_bg{0%{background-position:top}50%{background-position:bottom}100%{background-position:top}}@keyframes text_bg{0%{background-position:top}50%{background-position:bottom}100%{background-position:top}}

